                          :key="'configdata-1' + allConfigDataIndex">
                          <div
                            v-if="item.CORE_CONFIG_TYPE == 'dropdown' && item.CORE_CONFIG_KEY != 'TimeZone' && item.CORE_CONFIG_KEY != 'Currency'"
