                              </a>
                            </label>
                            <Field :rules="{ 'required': true }" name="item.CORE_CONFIG_DESC" v-slot="{ errors }">
                              <select :keys="item.CORE_CONFIG_ID" v-if="item.CORE_CONFIG_KEY != 'Currency'"
                                class="select form-control" v-model="item.CORE_CONFIG_VALUE"
                                name="EnableLocationTrackingModule">
                                <option v-for="(items, GetoptionIndex) in Getoption(item.CORE_CONFIG_OPTIONS)"
                                  :value="items" :key="GetoptionIndex">
                                  {{ items }}
                                </option>
                              </select>
                              <span name="EnableLocationTrackingModule" id="EnableLocationTrackingModule"
                                class="invalid-feedback d-block">{{ errors[0] }}</span>
                            </Field>
                          </div>
                        </div>
                        <div v-for="(item, allConfigIndex) in allProductConfigData"
                          class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6" :key="'configdata-2' + allConfigIndex">
                          <div v-if="item.CORE_CONFIG_TYPE == 'text'" class="form-group projectConfig">
                            <label>
                              <span> {{ item.CORE_CONFIG_DESC }} </span>
                              <span class="text-danger">*</span>
                              <a href="javascript:;" :data-content="item.CORE_CONFIG_DESC" data-toggle="popoveruserguide">
                                <i class="fa fa-question-circle-o text-popover" style="font-size: 14px"></i>
                              </a>
                            </label>
                            <!-- <Field name="LocationTracking" v-model="item.CORE_CONFIG_VALUE"
                                    :rules="{ 'required': true }" name="item.CORE_CONFIG_VALUE"
                                     v-slot="{  errors }"> -->

                            <input :keys="item.CORE_CONFIG_ID" type="text" :name="item.CORE_CONFIG_DESC"
                              :value="item.CORE_CONFIG_VALUE" class="textfield form-control" />
                            <span name="LocationTracking" id="LocationTracking" class="invalid-feedback d-block">
                            </span>
                            <!-- </Field> -->
                          </div>
                        </div>
                       
                       
                       
                        <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6" id="radios" type="radiobutton"
                          v-for="(item, index) in allProductConfigData">
                          <label v-if="item.CORE_CONFIG_TYPE == 'radiobutton'">
                            <span> {{ item.CORE_CONFIG_DESC }} </span>
                            <span class="text-danger">*</span>
                            <a href="javascript:;" data-toggle="popoveruserguide" :data-content="item.CORE_CONFIG_DESC"
