                              class="btn btn-secondary formbtn" href="javascript:;" title="Cancel"><i
                                class="fa fa-save pr-2"></i>Cancel</a> &nbsp;
                            <a id="hlkSave" @click="SaveConfiguration(values, errors)" issecurityprompt="IsSecurityPrompt"
